<template> 
  <div class="selectcourse">
          
    <el-form :inline="true" :model="form" class="form-inline-query">
      <el-form-item label="课程编号" >
        <el-input v-model="form.num" placeholder="请输入课程编号" />
      </el-form-item>
      <el-form-item label="课程名">
        <el-input v-model="form.name" placeholder="请输入课程名"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button class="commButton" size="mini" @click="doQuery()" type="primary">查询</el-button>
      </el-form-item>
    </el-form> 
<el-scrollbar height="800px">
    <el-table  class="table-content"
    v-loading="loading" :data="dataList"
        border
        style="width: 100%;"
    >
      <el-table-column
          label="序号"
          fixed="left"
          width="50"          
          align="center"
          color="black"
          >
          <template v-slot="scope">
              {{ scope.$index+1 }}
          </template>
      </el-table-column>

        <el-table-column
        label="课程编号"
        align="center"
        color="black"
        sortable
        prop="courseNum" />

        <el-table-column
        label="课程名称"
        align="center"
        color="black"
        prop="courseName" />

        <el-table-column
        label="课程教师"
        align="center"
        color="black"
        sortable
        prop="courseteacher" />

        <el-table-column
        label="课程描述"
        align="center"
        color="black"
        sortable
        prop="courseinfo" />

        <el-table-column
        label="课程上课日"
        align="center"
        color="black"
        sortable
        prop="courseday" />

        <el-table-column
        label="课程上课节次"
        align="center"
        color="black"
        sortable
        prop="courseorder" />

        <el-table-column
        label="课程学分"
        align="center"
        color="black"
        sortable
        prop="coursescore" />

        <el-table-column
        label="课程选课结束时间"
        align="center"
        color="black"
        sortable
        prop="courseselectend" />

        <el-table-column
            label="操作"
            align="center"
            color="black"
        >
        <template v-slot="scope">
            <el-button type="primary" size="mini" @click="doselect(scope.row.courseId, scope.row.courseNum, scope.row.courseName, 
            scope.row.courseteacher,scope.row.courseinfo, scope.row.courseday, scope.row.courseorder, scope.row.coursescore, scope.row.courseselectend, )">选课</el-button>
        </template>
        </el-table-column>
      </el-table>
      </el-scrollbar>
    </div>
</template>

<script>
import { ElMessage } from 'element-plus'
import {getCourseList,selectcourseSubmit} from '@/service/genServ.js'
export default {
  name: 'selectcourse',
  data() {
    return {
        sid:localStorage.getItem('sid'),
        usernum:localStorage.getItem('usernum'),
        username:localStorage.getItem('username'),
      form: {
        num:'',
        name:'',
      },
      dataList:[]
    }
  },
  created() {
      this.sid=localStorage.getItem('sid'),
      this.usernum=localStorage.getItem('usernum'),
      this.username=localStorage.getItem('username'),
      this.doQuery();
  },
  methods: {
    doQuery() {
       getCourseList({'num':this.form.num, 'name':this.form.name}).then(res => {
        this.dataList = res.data;
      })
    },

    doselect(courseId,courseNum,courseName,courseteacher,courseinfo,courseday,courseorder,coursescore,courseselectend) {
      var d1 = new Date();
      var d2 = new Date(courseselectend);
      if (d1.getTime()>d2.getTime()) {
        ElMessage({
          message: '选课时间已过',
          type: 'warning',
        })
      } 
      else{
       selectcourseSubmit({'sid':this.sid,'usernum':this.usernum,'username':this.username,'courseId':courseId,'courseNum':courseNum,'courseName':courseName,
       'courseteacher':courseteacher,'courseinfo':courseinfo,'courseday':courseday,'courseorder':courseorder,'coursescore':coursescore,'courseselectend':courseselectend}).then(res => {
        console.log(res)
        if(res.data==="1"){
        ElMessage({
          message: '课程已选！',
          type: 'error',
        })
        }
        else if(res.data==="2"){
        ElMessage({
          message: '时间冲突！',
          type: 'error',
        })
        }
        else{
        ElMessage({
          message: '选课成功！',
          type: 'success',
        })
        }
        this.doQuery();
      })
      }

    },

  }
}
</script>

<style scoped>
.selectcourse{
  width: 1650px;
  position:absolute;
  top:100px;
  left:225px;
}
</style>
